import React from 'react';
import './drinks-list-item.scss'

function DrinksListItem({ drink }) {

    return (<div className='drinks-list-item'>

        <h3 className='drinks-list-item'>
            <span className='gr-down'>
                {drink.name}
            </span>
        </h3>

        <section className='drinks-list-item'>
            <p className='center'>
                {drink.ingredients
                    .map((v, i, a) => {
                        const spacer = i < a.length - 1
                            ? <em key={drink.id + '-e' + i} className='drinks-list-item' >&nbsp;</em>
                            : ''

                        return (<span key={drink.id + '-s' + i} className='drinks-list-item'>
                            {v}{spacer}
                        </span>)
                    })
                }
            </p>
        </section>
    </div >)
}

export default DrinksListItem