import React from 'react';
import './drink.scss'
import { connect, batch } from 'react-redux';
import { showLoading } from '../app/app-actions'
import { usbAddMsgToDevice } from '../usb/usb-actions'
import { chooseDrink } from './drinks-actions'

const stateToProps = state => {
    return { drink: state.drinks.drink };
};

function dispatchToProps(dispatch) {
    return {
        chooseDrink: d => {
            // https://redux.js.org/faq/performance/#how-can-i-reduce-the-number-of-store-update-events
            batch(() => {
                dispatch(chooseDrink(d))
                dispatch(usbAddMsgToDevice(d.id))
                dispatch(showLoading())
            })
        }
    };
}

function Drink({ drink, showKinds, showDrinks, chooseDrink }) {
    return (
        <div className='drink'>

            <div className='drink-img'>
                <img className="drink center"
                    src={drink.icon}
                    alt={drink.name}></img>
            </div>

            <section className='drink'>

                <h3 className='drink'>
                    <span className='gr-down-left'>
                        {drink.name}
                    </span>
                </h3>

                <div className='drink-ingredients'>
                    <ul className='drink center'>
                        {drink.ingredients
                            .map((v, i, a) => (
                                <li key={drink.id + '-s' + i}
                                    className='drink' >
                                    {v}
                                </li>)
                            )
                        }
                    </ul>
                </div>

                <aside className='drink'>
                    <button className='gr-down'
                        onClick={() => chooseDrink(drink)}>
                        ZRÃ“B DRINKA
                        </button>
                </aside>

            </section>
        </div>)
}

export default connect(stateToProps, dispatchToProps)(Drink);